<template>
    <section id="section1">
            <div class="slide-container">
                <div class="slide-content">
                    <ul class="slide-wrap">
                        
                        <li 
                            
                            v-for="item,idx in 슬라이드"
                            :class="['slide', 'slide'+(idx+1)]"
                            :key="item"
                        >
                            <a href="#" :title="item">
                                <span>{{item}}</span>
                            </a>
                        </li>
                        
                    </ul>
                </div>
            </div>
        </section>
</template>

<script>
import $ from "jquery"

export default {
    name:'Section1Component',
    props:[],
    data(){
        return{
            슬라이드 :[
                'JUST 쇼핑몰 오픈!',
                '종합 쇼핑의 새 막을 연다! JUST 쇼핑몰!',
                'JUST쇼핑몰 9월 세일 이벤트 안내'
            ]
        }
    },
    created(){//태그가 완성된 뒤에 제이쿼리 사용 가능
        //섹션1 메인슬라이드 실행 (각 섹션마다 각 created 훅 안에 넣어주면 된다. )

        //1. 변수   
        let cnt=0;

        //2. 메인슬라이드함수 3개 우측에서 좌측으로 이동  0 1 2
        function mainSlide(){ 
            console.log("현재: " + (cnt-1<0?2:cnt-1),  "다음: " + cnt)                
            $('.slide').css({zIndex:1,opacity:1});
            $('.slide').eq(cnt)  .css({zIndex:2});
            $('.slide').eq( cnt-1 < 0 ? 2 : cnt-1 ).css({zIndex:3}).animate({opacity: 0}, 1000);
        }

        //3. 다음카운트함수
        function nextCount(){
            cnt++;                
            if(cnt>2) cnt=0;
            mainSlide();

        }

        //4. 자동타이머함수
        function autoTimer(){
            setInterval(nextCount, 3000);
        }
        autoTimer();
    }
}
</script>

<style scoped>
#wrap #main #section1 {width: 100%;height: 300px;background-color: #c9c;}
/* 메인슬라이드 아래에서 위로이동 */
#wrap #main #section1 .slide-container {width:100%;height: 100%;}
#wrap #main #section1 .slide-container .slide-content {width: 100%;height: 100%;overflow: hidden;}
#wrap #main #section1 .slide-container .slide-content .slide-wrap {
    width: 100%; 
    height: 100%;   
    position: relative;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url() no-repeat 50% 50%;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide1 {
    background-image: url(./images/banner_img.jpg);
    background-position: 50% 30%;
    background-size: 120% auto;
    z-index: 3;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide2 {
    background-image: url(./images/banner_img2.jpg);
    background-position: 50% 0%;
    background-size: 100% auto;
    z-index: 2;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide3 {
    background-image: url(./images/banner_img3.png);
    background-position: 50% 30%;
    background-size: 110% auto;
    z-index: 1;
}

#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide  a {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide  a span {
    width: 100%;
    height: 70px;
    background: rgba(85, 170, 204, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28px;
    color:#fff;
    user-select: none;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide2 a span {background-color: rgba(204, 85, 170, 0.9);}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide3 a span {background-color: rgba(250, 120, 85, 0.9);}





</style>