<template>
    <header id="header">
            <div class="left">
                <h1><a href="./index.html" :title="logoTitle.title1 +''+ logoTitle.title2"><span>{{logoTitle.title1}}</span> <span>{{logoTitle.title2}}</span></a></h1>
            </div>
            <div class="right">
                <nav id="nav">
                    <ul>
                        <li 
                            @mouseleave="onMouseOutNav"
                            class="col" 
                            v-for="(item,i) in 카테고리" 
                            :key="item.메인메뉴"                            
                        >
                            <a 
                                @mouseenter="onMouseOverMainBtn(i)"
                                href="#" 
                                :class="['main-btn', {'on':isOn[i]}]" 
                                :title="item.메인메뉴"
                                @click.prevent="eventEmitMethod(item.메인메뉴)" 
                            >
                                {{item.메인메뉴}}
                            </a>
                            <div 
                                :class="['sub', 'sub'+(i+1)]"
                                v-show="isOn[i]"
                            >
                                <div class="sub-container">
                                    <ul>
                                        <li 
                                            v-for="(column,idx) in item.서브메뉴" 
                                            :key="idx"
                                        >
                                            <ul>
                                                <li 
                                                    v-for="row in column"  
                                                    :key="row"
                                                >
                                                    <a  
                                                        
                                                        href="#" 
                                                        :title="row"
                                                        @click.prevent="eventSub(row)"
                                                    >
                                                        {{row}}
                                                    </a>
                                                </li>
                                            </ul>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </li>
                    </ul>
                </nav>
            </div>
        </header>
</template>

<script>
export default {
    name:'HeaderComponent',
    props:['logoTitle'],
    data(){
        return{
          카테고리:[
            {
                메인메뉴:'탑',            
                서브메뉴: [
                    ['블라우스','티','셔츠','니트'],
                    ['자켓','코트','가디건','머플러'],
                    ['청바지','짧은바지','긴바지','레깅스'],
                    ['귀고리','목걸이','반지','팔찌']
                ]
            },
            {
                메인메뉴:'아우터', 
                서브메뉴: [
                    ['블라우스','티','셔츠','니트'],
                    ['자켓','코트','가디건','머플러'],
                    ['청바지','짧은바지','긴바지','레깅스'],
                    ['귀고리','목걸이','반지','팔찌']
                ]
            },
            {
                메인메뉴:'팬츠', 
                서브메뉴: [
                    ['블라우스','티','셔츠','니트'],
                    ['자켓','코트','가디건','머플러'],
                    ['청바지','짧은바지','긴바지','레깅스'],
                    ['귀고리','목걸이','반지','팔찌']
                ]
            },
            {
                메인메뉴:'악세사리', 
                서브메뉴: [
                    ['블라우스','티','셔츠','니트'],
                    ['자켓','코트','가디건','머플러'],
                    ['청바지','짧은바지','긴바지','레깅스'],
                    ['귀고리','목걸이','반지','팔찌']
                ]
            }
          ],
          isOn: [false,false,false,false]
        }
    },
    methods:{
        onMouseOverMainBtn(i){
            this.isOn = [false,false,false,false]
            this.isOn[i] = true
        },
        onMouseOutNav(){
            this.isOn = [false,false,false,false]
        },
        eventEmitMethod(z){ 
            // 전송을 하는 역할 => $emit 이밋 
            // 메뉴를 선택하여 부모 컴포넌트WrapComponent 에게 전달한다.
            // 자식 => 부모 통신 $emit 이밋
            // 이밋은 이벤트 버스가 없을 때 거의 똑같다.
            // this == HeaderComponent
            // this.$emit('전송이벤트이름', '매개변수');
            this.$emit('eventEmit', z);
        },
        eventSub(y){
            this.$emit('eventSubEmit', y);
            console.log(y);
        }
    }
}
</script>

<style scoped>

/* 헤더 */
#wrap #header {width: 100%;height: 100px;display: flex;position: relative;z-index: 3;}

/* 로고 */
#wrap #header .left {width: 200px;height: 100%;display: flex;align-items: center;}
#wrap #header .left h1 {width:100%;height: 40px;}
#wrap #header .left h1 a {
    font-size: 24px;
    font-weight: 600;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}
#wrap #header .left h1 a span:nth-child(1) {
    color: #5ac;
}
#wrap #header .left h1 a span:nth-child(2) {
    margin-left: 5px;
    color: #c5a;
}

#wrap #header .right {
    width: 1000px;height: 100%;    
}


/* 네비게이션 =>  메인메뉴 */
#wrap #header .right #nav {
    width:700px;
    margin:49px 20px 0 auto; 
}
#wrap #header .right #nav > ul {display: flex;width:100%;}
#wrap #header .right #nav > ul > li {
    width:25%;
    border-top:1px solid #ddd;
    border-bottom:1px solid #ddd;
    border-left:1px solid #ddd;
}
#wrap #header .right #nav > ul > li:last-child {border-right:1px solid #ddd;}

#wrap #header .right #nav > ul > li > a {
    width: 100%;display: flex;align-items: center;justify-content: center;
    height: 50px;
    font-size: 15px;
    font-weight: 600;
    color: #000;
    transition: all 0.3s;
    background: #fff;
}
#wrap #header .right #nav > ul > li > a:hover {
    background: #2c2a29;
    color: #669900;
}   
#wrap #header .right #nav > ul > li > a.on {
    background: #2c2a29;
    color: #669900;
}   


/* 서브메뉴 */
/* 스타벅스 서브메뉴와 같은 전체메뉴 */
#wrap #header .right #nav > ul > li .sub {
    position: absolute;
    top:100px;
    left:0;
    width: 100%;
    background: #2c2a29;
}
/* #wrap #header .right #nav > ul > li .sub1 {background-color: #333329;}
#wrap #header .right #nav > ul > li .sub2 {background-color: #2c2a59;}
#wrap #header .right #nav > ul > li .sub3 {background-color: #332a29;}
#wrap #header .right #nav > ul > li .sub4 {background-color: #2c4a29;} */


#wrap #header .right #nav > ul > li .sub .sub-container {
    width:700px;
    margin: 0 20px 0 auto;
}
#wrap #header .right #nav > ul > li .sub .sub-container > ul {width:100%;display: flex;padding: 20px 0;}
#wrap #header .right #nav > ul > li .sub .sub-container > ul > li {width:25%;} /* 4칸 */
#wrap #header .right #nav > ul > li .sub .sub-container > ul > li > ul {width:100%;}
#wrap #header .right #nav > ul > li .sub .sub-container > ul > li > ul > li {width:100%;}
#wrap #header .right #nav > ul > li .sub .sub-container > ul > li > ul > li a {
    color:#999;
    display: flex;
    height: 30px;
    width: 100%;
    align-items: center;
    justify-content: center;
}
#wrap #header .right #nav > ul > li .sub .sub-container > ul > li > ul > li a:hover {
    background: #666;
    color: #fff;
}

</style>